<template>
  <div class="system">
    <j-header title="设 置"></j-header>
    <main>
      <div class="userinfo">
        <img :src="userInfo.picture" alt="">
        <ul>
          <li>{{ userInfo.username }}</li>
          <li>会员名：{{ userInfo.username }}</li>
        </ul>
      </div>
      <div class="audio">
        <h2>音效</h2>
        <div class="min-box" @click="jswitch" v-waves>
          <div>
            <p>app内置音效</p>
            <p>包括刷新、点击切换页面等操作反馈音效</p>
          </div>
          <svg-icon icon-class="choose" :class="audioSwitch"></svg-icon>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
// @ is an alias to /src
import JHeader from '@/components/JHeader'
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'system',
  components: {
    JHeader
  },
  data () {
    return {
    }
  },
  computed: {
    ...mapGetters([
      'userInfo',
      'audioSwitch'
    ])
  },
  created () {
  },
  methods: {
    ...mapActions([
      'setAudio'
    ]),
    jswitch () {
      this.audioSwitch === 'on' ? this.setAudio('off') : this.setAudio('on')
    }
  },
  mounted () {
  }
}
</script>

